<template>
    <div class="statistic">
        <div class="title">问卷名称：{{name}}</div>
        <div class="content">
            <div class="item" v-for = "(item, index) in data" :key="index">
                <div class="title">{{item.title}}</div>   
                <div v-if="item.type == 0" class="myChart" :data-index="index" :style="{width: '200px', height: '200px'}"></div>
                <div class="list" v-if="item.type == 1">
                    <div class="list-item" v-for = "(item2, index2) in item.answerList" :key="index2">{{item2.userName}}: {{item2.content}}</div>
                </div>
            </div>
        </div>
        <div class="footer">
            <el-button @click="btnClick()" class="btn" type="primary">取消</el-button>
        </div>
    </div>
</template>

<script>
export default {
    name: "statistic",
    data() {
        return {
            loading: false,
            id: "",
            type: "",
            name: "",
            data: "",
        };
    },
    mounted() {
        this.init();
        this.search();
    },
    methods: {
        init() {
            const { type, id, name } = this.$route.query;
            this.type = type;
            this.id = id;
            this.name = name;
        },
        echartsInit() {
            const mycharts = document.querySelectorAll('.myChart');
            this.data.forEach((item, index) => {
                // 选择题
                if(item.type == 0){
                    const ansTotal = item.answerList.length;
                    let ansName = [item.item1, item.item2, item.item3, item.item4, item.item5];
                    let ansNum = [0, 0, 0, 0, 0];

                    item.answerList.forEach(obj => {
                        if(obj.item1) ansNum[0]++;
                        if(obj.item2) ansNum[1]++;
                        if(obj.item3) ansNum[2]++;
                        if(obj.item4) ansNum[3]++;
                        if(obj.item5) ansNum[4]++;
                    });
                    let myChart = this.echarts.init(mycharts[index]);
                    let seriesData = [];
                    ansName.forEach((item, index) => {
                        if(item){
                            let obj = {
                                value: ansNum[index],
                                name: item
                            }
                            seriesData.push(obj);
                        } 
                    })
                    myChart.setOption({
                        tooltip : {
                            trigger: 'item',
                            formatter: "{a} <br/>{b} : {c}人 ({d}%)"
                        },
                        series : [
                            {
                                name: item.title,
                                type: 'pie',
                                radius : '55%',
                                center: ['50%', '60%'],
                                data: seriesData
                            }
                        ]
                    });
                }
                // 问答题
                if(item.type == 1){

                }
            })
        },
        search() {
            //搜索
            this.loading = true;
            this.api.get(`/survey/${this.id}/answer`).then(res => {
                this.loading = false;
                this.data = res.data.questionList;
                setTimeout(()=>{
                    this.echartsInit(res);
                },20)
            })
            .catch(err => {
                console.log(err);
                this.loading = false;
                this.$message(err.message);
            });
        },
        btnClick(){
            this.$router.go(-1)
        },
        dataInit() {
            //数据初始化
        }
    }
};
</script>
 
<style lang="less" scoped>
.statistic {
    padding: 40px;
    .title{
        font-size: 18px;
        font-weight: 600;
        padding-bottom: 30px;
    }
    .item{
        .title{
            font-size: 16px;
            padding: 0;
            margin-bottom: 10px;
        }
        .list{
            padding-top: 10px;
            height: 100px;
            overflow: auto;
            width: 50%;
            .list-item{
                padding: 5px 0;
            }
        }
    }
    .footer{
        margin-top: 20px;
    }
}
</style>
 